.container {
  position: relative;
  width: 100%;
  height: 50px;
  padding: 0px 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: black;
  z-index: 10;
}

/* Have to Seperate because the Search bar has a tag! */
.GroupLink a {
  margin: 0px 20px;
  position: relative;
  top: 20%;
  font-size: 20px;
}

.pageName a:link {
  color: white;
}

.pageName a:visited {
  color: white;
}

.pageName {
  position: relative;
  top: 20%;
  font-size: 20px;
  font-weight: bold;
}
